.hi-alert{position:relative;padding:9px 12px;background:#ebf2fd;border:1px solid #b3cdfb;border-radius:2px;color:#4284f5;font-size:14px;min-width:280px;display:block;-webkit-box-align:start;-ms-flex-align:start;align-items:flex-start}.hi-alert .hi-icon__title{display:-webkit-box;display:-ms-flexbox;display:flex;font-size:14px;-webkit-box-align:center;-ms-flex-align:center;align-items:center;line-height:22px}.hi-alert .hi-icon__title .hi-icon{font-size:16px;margin-right:8px}.hi-alert .text-message{margin:2px 24px 0 24px;font-weight:400;font-size:12px;line-height:20px;color:#666}.hi-alert .close-btn{position:absolute;top:12px;right:12px;cursor:pointer}.hi-alert .close-btn .hi-icon{font-size:14px;color:#999}.hi-alert.error{background:#fceded;border:1px solid #f7b9b9;color:#eb5252}.hi-alert.error .icon-contain{color:#eb5252}.hi-alert.success{background:#e8f6ee;border:1px solid #a4dbba;color:#1da653}.hi-alert.success .icon-contain{color:#1da653}.hi-alert.warning{background:#fbf5e6;border:1px solid #f3d79d;color:#e19d0c}.hi-alert.warning .icon-contain{color:#e19d0c}.hi-alert.noTitle .text-contain .text-message{font-size:14px}
