.hi-collapse{text-align:left;border:1px solid #d9d9d9;border-bottom:0;border-radius:3px;font-size:14px}.hi-collapse .collapse-item{border-bottom:1px solid #d9d9d9}.hi-collapse .collapse-item__head{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;background-color:#f7f7f7;padding:12px 0 12px 16px;color:rgba(0,0,0,0.85);cursor:pointer}.hi-collapse .collapse-item__head .collapse-item__title{-webkit-box-flex:1;-ms-flex:auto;flex:auto;line-height:1}.hi-collapse .collapse-item__head>.collapse-item__icon{-webkit-box-flex:0;-ms-flex:none;flex:none;margin-right:10px;-webkit-transition:all 0.2s ease-out;transition:all 0.2s ease-out}.hi-collapse .collapse-item__content{display:block;width:100%;-webkit-transition:all 0.2s ease-out;transition:all 0.2s ease-out;height:0;-webkit-transform-origin:top;transform-origin:top;padding:0 16px;color:rgba(0,0,0,0.65);overflow:hidden;-webkit-box-sizing:border-box;box-sizing:border-box}.hi-collapse .collapse-item--show>.collapse-item__head>.collapse-item__icon{-webkit-transform:rotate(90deg);transform:rotate(90deg)}.hi-collapse .collapse-item--show>.collapse-item__content{padding:16px;height:auto;width:100%;border-top:1px solid #d9d9d9}.hi-collapse .collapse-item--disabled .collapse-item__head{cursor:not-allowed}.hi-collapse__simple .collapse-item .head{background-color:#fff}
