.hi-card{border-radius:2px;border:1px solid #e7e7e7;-webkit-box-sizing:border-box;box-sizing:border-box;background:#fff}.hi-card--padding{padding:24px}.hi-card--hover:not(.hi-card--disabled):hover{-webkit-box-shadow:0 2px 8px 0 rgba(0,0,0,0.08);box-shadow:0 2px 8px 0 rgba(0,0,0,0.08)}.hi-card--disabled{background:#f6f6f6;color:#ccc;cursor:not-allowed;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.hi-card__header{height:24px;line-height:24px;display:-webkit-box;display:-ms-flexbox;display:flex;padding-bottom:12px;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between}.hi-card__header--onlyextra{-webkit-box-pack:end;-ms-flex-pack:end;justify-content:flex-end}.hi-card__title{font-size:16px;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}.hi-card__content--cover{padding:16px}.hi-card__content--simple{height:40px;line-height:40px;padding:0 12px;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}.hi-card__extra{cursor:pointer;color:#4284f5;font-size:14px}.hi-card__extra--hide{display:none}.hi-card--middle{width:376px}.hi-card--small{width:276px}.hi-card--large{width:576px}.hi-card--simple-small{width:136px}.hi-card--simple-middle{width:156px}.hi-card--simple-large{width:176px}
