.hi-modal{position:fixed;top:0;left:0;right:0;bottom:0;z-index:1000}.hi-modal--hide{display:none !important}.hi-modal__mask{position:absolute;top:0;left:0;right:0;bottom:0;z-index:1000;width:100%;height:100%;background-color:rgba(0,0,0,0.54);-webkit-transition:all .15s linear;transition:all .15s linear}.hi-modal__dialog{position:relative;top:10%;z-index:1001;-webkit-box-sizing:border-box;box-sizing:border-box;width:600px;max-height:80%;margin:0 auto;border-radius:2px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;font-size:14px;background-color:#fff;-webkit-box-shadow:0 4px 16px 0 rgba(0,0,0,0.1);box-shadow:0 4px 16px 0 rgba(0,0,0,0.1);-webkit-transition:all .15s linear;transition:all .15s linear}.hi-modal__dialog--large{width:800px}.hi-modal__dialog--small{width:360px}.hi-modal__header{-webkit-box-flex:0;-ms-flex:none;flex:none;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;height:56px;border-bottom:1px solid #e7e7e7}.hi-modal__title{-webkit-box-flex:1;-ms-flex:1;flex:1;margin:0;padding:0 16px;font-size:16px;font-weight:400;text-align:left;color:#333}.hi-modal__close{width:56px;height:56px;font-size:16px;line-height:56px;text-align:center;color:#666;cursor:pointer;-webkit-transition:color .15s linear;transition:color .15s linear}.hi-modal__close:hover,.hi-modal__close:active{color:#333}.hi-modal__content{-webkit-box-flex:1;-ms-flex:auto;flex:auto;-webkit-box-sizing:border-box;box-sizing:border-box;min-height:54px;padding:16px;overflow-x:hidden;overflow-y:auto}.hi-modal__footer{-webkit-box-flex:0;-ms-flex:none;flex:none;display:-webkit-box;display:-ms-flexbox;display:flex;border-top:1px solid #e7e7e7;padding:12px 16px;-webkit-box-pack:end;-ms-flex-pack:end;justify-content:flex-end}
