.hi-preview{position:fixed;top:0;left:0;right:0;bottom:0;background:rgba(0,0,0,0.54);z-index:9999}.hi-preview__image:hover{cursor:move}.hi-preview-toolbar{position:absolute;bottom:20px;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);background:#fff;padding:0 32px;height:48px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;border-radius:28px}.hi-preview-toolbar .hi-icon{margin:0 12px;font-size:20px;cursor:pointer}.hi-preview__close{position:absolute;right:10px;top:10px;width:36px;height:36px;line-height:1;border-radius:50%;text-align:center}.hi-preview__close img{width:100%;height:100%;cursor:pointer}
