.hi-cascader__popper{display:-webkit-box;display:-ms-flexbox;display:flex;background-color:#fff;font-size:14px;-webkit-box-shadow:0 2px 8px rgba(0,0,0,0.15);box-shadow:0 2px 8px rgba(0,0,0,0.15)}.hi-cascader-menu{height:180px;min-width:110px;overflow:auto}.hi-cascader-menu:not(:last-child){border-right:1px solid #e8e8e8}.hi-cascader-menu,.hi-cascader-menu__item{margin:0;padding:0;list-style:none}.hi-cascader-menu__item{position:relative;padding:5px 12px;color:#48576a;cursor:pointer}.hi-cascader-menu__item.hi-cascader-menu__item-disabled{color:#bfcbd9;cursor:not-allowed}.hi-cascader-menu__item.hi-cascader-menu__item-expanded{padding-right:25px}.hi-cascader-menu__item.hi-cascader-menu__item-active{background-color:#f5f5f5}.hi-cascader-menu__item:hover{background-color:#f5f5f5}.hi-cascader-menu__item--icon{position:absolute;right:12px}.hi-cascader-menu__item--label-hightlight{color:#f5222d}.hi-cascader-menu__item--label-split{margin:0 3px}
