.login{position:relative;display:-webkit-box;display:-ms-flexbox;display:flex;height:64px;-webkit-box-align:center;-ms-flex-align:center;align-items:center;padding:0 16px;font-size:14px;line-height:1.5;cursor:pointer}.login::after{margin-left:8px;border:5px solid transparent;border-bottom-width:0;content:'';border-top-color:#fff}.login--active::after{border-top-width:0;border-bottom-width:5px;border-top-color:#fff;border-bottom-color:#fff}.login--empty::after{display:none}.login__img{display:inline-block;width:24px;height:24px;margin-right:8px;border-radius:50%;line-height:24px;text-align:center;background-repeat:no-repeat;background-position:50% 50%;background-size:cover}.login__name{display:inline-block;max-width:120px;text-overflow:ellipsis;white-space:nowrap;overflow:hidden}.login__info{display:none;position:absolute;right:10px;top:56px;padding:10px;border:1px solid #d8d8d8;border-radius:4px;line-height:20px;background-color:#fff;cursor:default}.login--active .login__info{display:block}.login__info::before{position:absolute;top:-6px;right:24px;-webkit-box-sizing:border-box;box-sizing:border-box;width:10px;height:10px;border-right:1px solid #d8d8d8;border-top:1px solid #d8d8d8;content:'';background-color:#fff;-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}
