.hi-rate{list-style:none;display:inline-block;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding-left:0 !important;margin:0 !important}.hi-rate__star{cursor:pointer;position:relative;display:inline-block;-webkit-transition:0.3s ease;transition:0.3s ease;margin:0 6px !important;width:24px;height:24px}.hi-rate__star:active,.hi-rate__star:hover{-webkit-transform:scale(1.2);transform:scale(1.2)}.hi-rate__star--disabled{cursor:not-allowed}.hi-rate__star--disabled:hover{-webkit-transform:none;transform:none}.hi-rate__star__half{height:100%;width:50%;display:inline-block;position:absolute;z-index:1}.hi-rate__star__half--wrapper{position:absolute;top:0;left:0;height:24px;width:24px}.hi-rate__star__half--right{right:0}.hi-rate__icon{display:inline-block;width:100%;height:100%}.hi-rate__icon svg{width:100%;height:100%}
