.hi-popover{position:relative;display:inline-block}.hi-popover-base{background:#fff;padding:0 5px;white-space:nowrap;-webkit-transition:all 0.5s;transition:all 0.5s;border-radius:4px;-webkit-box-shadow:0 2px 8px rgba(0,0,0,0.15);box-shadow:0 2px 8px rgba(0,0,0,0.15)}.hi-popover-base::after{content:'';position:absolute;width:10px;height:10px;border:1px solid #e8e8e8;background:#fff;-webkit-box-sizing:border-box;box-sizing:border-box}.hi-popover-base.hi-popover-show{display:block}.hi-popover-base .hi-popover__title{padding:5px 15px;color:#000;border-bottom:1px solid #e8e8e8}.hi-popover-base .hi-popover__content{padding:12px 15px}.hi-popover-top{position:relative;margin-bottom:10px}.hi-popover-top::after{border-top-color:transparent;border-right-color:transparent;bottom:-5px;left:50%;-webkit-transform:translateX(-50%) rotate(-45deg);transform:translateX(-50%) rotate(-45deg)}.hi-popover-right{position:relative;margin-left:10px}.hi-popover-right::after{border-bottom-color:transparent;border-right-color:transparent;left:-5px;top:50%;-webkit-transform:translateY(-50%) rotate(-45deg);transform:translateY(-50%) rotate(-45deg)}.hi-popover-bottom{position:relative;margin-top:10px}.hi-popover-bottom::after{border-left-color:transparent;border-bottom-color:transparent;top:-5px;left:50%;-webkit-transform:translateX(-50%) rotate(-45deg);transform:translateX(-50%) rotate(-45deg)}.hi-popover-left{position:relative;margin-right:10px}.hi-popover-left::after{border-top-color:transparent;border-left-color:transparent;right:-5px;top:50%;-webkit-transform:translateY(-50%) rotate(-45deg);transform:translateY(-50%) rotate(-45deg)}
